// 
const oCan = document.getElementById('canvas'), // 
  ctx = oCan.getContext('2d'),
  oShowImg = document.getElementById('lastImg'), // 
  oMsg = document.querySelector('.msg'),
  oZhezhao = document.querySelector('.zhezhao'),
  oRevoke = document.getElementById('revoke') //   

// 
function showLoading(type) {
  oZhezhao.style.display = type
  oMsg.style.display = type
}

export default {
  // 
  oCan: oCan,
  ctx: ctx,
  oShowImg: oShowImg,
  oMsg: oMsg,
  oZhezhao: oZhezhao,
  oRevoke: oRevoke,
  // 
  showLoading: showLoading,
  isDraw: false, // 
  isNewPic: true, // 
  isStopPrevent: true, // 
  data: {},
  imgData: {},
  // 
  transform: (obj, data) => {
    obj.style.WebkitTransform = data
    obj.style.transform = data
  }
}
