import opts from './opts'
import lineData from './lineData'
import setFilter from './filter'

// 
const arrX = [], // x
  arrY = [], // y
  arrN = [], // 
  arrColor = [], // 
  arrWidth = [], // 
  arrNumber = [], // 
  c_left = opts.oCan.offsetLeft, // canvasLeft,top
  c_top = opts.oCan.offsetTop,
  oCan = opts.oCan,
  ctx = opts.ctx
let lastX = -1,
  lastY = -1

function addData(data) {
  arrX.push(data.x)
  arrY.push(data.y)
  arrN.push(data.n)
  arrColor.push(data.color)
  arrWidth.push(data.width)
}

// 
function draw(len) {
  ctx.beginPath()
  ctx.lineCap = 'round'
  ctx.lineJoin = 'round'

  for (let i = 1; i < len; i++) {
    // 
    ctx.lineWidth = arrWidth[i]
    ctx.strokeStyle = arrColor[i]
    lastX = arrX[i]
    lastY = arrY[i]
    if (arrN[i] === 2) {
      // 
      ctx.beginPath()
      ctx.save()
      ctx.moveTo(lastX, lastY)
    } else {
      ctx.lineTo(lastX, lastY)
    }
    // 
    ctx.stroke()
    ctx.restore()
  }
  ctx.closePath()
}

// 
function cleanDraw() {
  draw(arrN.length)
}

// 
function move(e) {
  e.preventDefault()
  const t = e.touches[0]
  // xy
  arrX.push(Math.floor(t.pageX - c_left))
  arrY.push(Math.floor(t.pageY - c_top))
  arrN.push(1)
  arrWidth.push(lineData.w)
  arrColor.push(lineData.color)
  // 
  draw(arrX.length)
}
// 
function down(e) {
  ctx.clearRect(0, 0, opts.canvasW, opts.cavasH)
  if (!opts.isDraw) {
    return false
  }
  // 
  if (opts.isNewPic) {
    opts.isNewPic = false
    // 
    arrX.length = 0
    arrY.length = 0
    arrN.length = 0
    arrColor.length = 0
    arrWidth.length = 0
  }
  const t = e.touches[0]
  // 
  addData({
    x: t.pageX - c_left,
    y: t.pageY - c_top,
    n: 2,
    color: lineData.color,
    width: lineData.w
  })
  // 
  arrNumber.push(arrN.length)
  oCan.addEventListener('touchmove', move, false)
}
// 
function up(e) {
  if (!opts.isDraw) {
    return false
  }
  const t = e.changedTouches[0]
  // 
  addData({
    x: t.pageX - c_left,
    y: t.pageY - c_top,
    n: 1,
    color: lineData.color,
    width: lineData.w
  })
  // 
  oCan.removeEventListener('touchmove', move, false)
}

// 
function revoke() {
  // 0
  if (!arrNumber[arrNumber.length - 1]) {
    return false
  }
  // 
  const len = arrNumber[arrNumber.length - 1]
  arrNumber.pop()
  // 
  arrX.length = len
  arrY.length = len
  arrN.length = len
  arrColor.length = len
  arrWidth.length = len
  ctx.clearRect(0, 0, opts.canvasW, opts.canvasH)
  const data = opts.data
  /*
    
     =>  => 
  */
  ctx.drawImage(data.img, data.imgPos.x, data.imgPos.y, data.imgPos.w, data.imgPos.h)
  setFilter()
  draw(len)
}

// 
opts.oRevoke.addEventListener('touchstart', revoke)

oCan.addEventListener('touchstart', down, false)
oCan.addEventListener('touchend', up, false)

// 
export default cleanDraw
